<template>
    <el-card class="box-card">
      <div>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="550px"
          class="demo-ruleForm"
        >
          <el-form-item label="系统名称" prop="systemname">
            <el-select v-model="ruleForm.systemname" placeholder="请选择系统名称">
              <el-option label="mockserver" value="mockserver"></el-option>
              <el-option label="mockweb" value="mockweb"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="请求方式" prop="type">
            <el-select v-model="ruleForm.type" placeholder="请选择请求方式">
              <el-option label="POST" value="post"></el-option>
              <el-option label="GET" value="get"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="服务名" prop="servicename">
            <el-input v-model="ruleForm.servicename"></el-input>
          </el-form-item>
          <el-form-item label="方法名" prop="methodname">
            <el-input v-model="ruleForm.methodname"></el-input>
          </el-form-item>
          <el-form-item label="请求报文" prop="request">
            <el-input type="textarea" v-model="ruleForm.request"></el-input>
          </el-form-item>
          <el-form-item label="返回报文" prop="response">
            <el-input type="textarea" v-model="ruleForm.response"></el-input>
          </el-form-item>
          <el-form-item label="有效标志" prop="flag">
            <el-select v-model="ruleForm.flag" placeholder="请选择有效标志">
              <el-option label="F" value="f"></el-option>
              <el-option label="A" value="a"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="button" style="margin-left:-60%">
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button type="info" plain @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
</template>

<script>
  export default {
    name: "interDetail",
    props: [ "params"],
    data() {
      return {
        
        ruleForm: {
          systemname: '',    //系统名称
          type: '',    //方法类型
          servicename: '',    //服务名
          methodname: '',    //方法名
          request: '',    //请求报文
          response: '',    //返回报文
          flag: ''   //有效标志位
        },

        //验证规则
        rules: {
          systemname: [
            { required: true, message: '请选择系统名称', trigger: 'change' },
          ],
          type: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
          servicename: [
            { required: true, message: '请输入服务名', trigger: 'blur' }
          ],
          methodname: [
            { required: true, message: '请输入方法名', trigger: 'blur' }
          ],
          request: [
            { required: true, message: '请输入请求报文', trigger: 'blur' }
          ],
          response: [
            { required: true, message: '请输入返回报文', trigger: 'blur' }
          ],
          flag: [
            { required: true, message: '请选择有效标志', trigger: 'change' }
          ]
        }
      };
    },

    //初始化
    mounted() {
      var param={id:this.params.id};
        this.request.postJson(this.mockapi.detail, param).then(res =>{
          this.ruleForm.systemname = res.data.system;
          this.ruleForm.servicename = res.data.service;
          this.ruleForm.methodname = res.data.method;
          this.ruleForm.flag = res.data.flag;
          this.ruleForm.requesttime = res.data.opTime;
          this.ruleForm.type = res.data.type;
          this.ruleForm.request = res.data.request;
          this.ruleForm.response = res.data.response;
        });
    },
    watch:{
      params(){
           this.qryInterDetail(this.params.id)
      }
    },
    methods: {

      //查询接口信息并绑定数据
      qryInterDetail(id) {
        var param={id:id};
        this.request.postJson(this.mockapi.detail, param).then(res =>{
          this.ruleForm.systemname = res.data.system;
          this.ruleForm.servicename = res.data.service;
          this.ruleForm.methodname = res.data.method;
          this.ruleForm.flag = res.data.flag;
          this.ruleForm.requesttime = res.data.opTime;
          this.ruleForm.type = res.data.type;
          this.ruleForm.request = res.data.request;
          this.ruleForm.response = res.data.response;
        });
      },

      //接口修改方法
      submitForm(formName) {
        var param = {
          id:this.params.id,
          systemName:this.ruleForm.systemname,
          type:this.ruleForm.type,
          service:this.ruleForm.servicename,
          method:this.ruleForm.methodname,
          request:this.ruleForm.request,
          response:this.ruleForm.response,
          flag:this.ruleForm.flag,
        };
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.request.postJson(this.mockapi.modify, param).then(res =>{
              console.log(res);
              this.$message({
                type: 'success',
                message: '修改成功!',
              });
              
            })
            .then(function(res){
              
            })
            .catch(function(res){
              
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>
<style scoped src="./detailandedit.css"></style>



